import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { KitchenStaffManagerComponent } from './kitchen-staff-manager.component';
import { UserListComponent } from './user-list/user-list.component';
import { CreateUserComponent } from './create/user-create.component';
import { EditUserComponent } from './edit-user/edit-user.component';
import { KitchenStaffDetailResolver } from './detail/staff-detail-resolver.service';
import { UserDetailComponent } from './detail/user-detail.component';


const routes: Routes = [
  {
    path: '',
    component: KitchenStaffManagerComponent,
    children: [
      { path: '', redirectTo: 'list', pathMatch: 'full' },

      {
        path: 'list',
        component: UserListComponent
      },
      {
        path: 'create',
        component: CreateUserComponent
      },
      {
        path: 'edit/:id',
        component: EditUserComponent,
        resolve: { staff: KitchenStaffDetailResolver }
      },
      {
        path: 'detail/:id',
        component: UserDetailComponent,
        resolve: { staff: KitchenStaffDetailResolver }
      },
      { path: '**', redirectTo: 'list' }

    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
  providers: [KitchenStaffDetailResolver]
})
export class KitchenStaffManagerRoutingModule { }
