import { Component, OnInit, OnDestroy, Input } from '@angular/core';
import { Router } from '@angular/router';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { AuthUserService } from 'src/app/core/auth-user.service';
import { IUser } from 'src/app/core/user.model';
import { KitchenStaffManagerService } from '../kitchen-staff-manager.service';

@Component({
  selector: 'app-user-form',
  templateUrl: './user-form.component.html'
})
export class UserFormComponent implements OnInit, OnDestroy {
  private ngUnsubcribe = new Subject();
  staffForm: FormGroup;
  progressing = false;
  error: string;

  @Input()
  operation: 'CREATE' | 'UPDATE' = "CREATE";

  @Input()
  staff: any;

  vendorList$ = this.staffService.vendorList$;
  areaList$ = this.staffService.areaList$;

  user: IUser;
  constructor(private authUserService: AuthUserService,private router: Router, private formBuilder: FormBuilder, private staffService: KitchenStaffManagerService) {

  }

  ngOnInit(): void {
    console.log('staff: ', this.staff);
    this.user = this.authUserService.user;
if (this.user.role =='Admin') {
  this.staffService.getAreaList();
}
    // if (this.operation === 'UPDATE') {
    //   this.staffForm = this.formBuilder.group({
    //     staff_id: this.staff.staff_id,
    //     name: [this.staff['name'], Validators.required],
    //     email: [this.staff['email'], [Validators.required, Validators.pattern(/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/)]],
    //     city: [this.staff['city'], Validators.required],
    //     role: [this.staff['role'], Validators.required],
    //     mobile: [this.staff['mobile'], [Validators.required, Validators.pattern(/^([0|\+[0-9]{1,5})?([7-9][0-9]{9})$/)]],
    //     status: [this.staff['status'], Validators.required],
    //     owner_id: this.staff.owner_id,
    //   });
    // } else {
      this.staffForm = this.formBuilder.group({
        staff_id: this.staff.staff_id,
        name: [this.staff['name'], Validators.required],
        email: [this.staff['email'], [Validators.required, Validators.pattern(/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/)]],
        password: [this.staff['password'], [Validators.required, Validators.minLength(6)]],
        confirmPassword: [this.staff['password'], Validators.required],
        city: [this.staff['city'], Validators.required],
        role: [this.staff['role'], Validators.required],
        mobile: [this.staff['mobile'], [Validators.required, Validators.pattern(/^([0|\+[0-9]{1,5})?([7-9][0-9]{9})$/)]],
        status: [this.staff['status'], Validators.required],
        owner_id: [this.user['vendor_id,'] || this.user._id, Validators.required],
      });
    // }
  }
  ngOnDestroy() {
    this.ngUnsubcribe.next();
    this.ngUnsubcribe.complete();
  }

  submit(): void {
    this.progressing = true;
    console.log('staff Form Values: ', this.staffForm.value);

    if (this.operation == 'UPDATE') {
      this.staffService.updateStaff(this.staffForm.value)
        .pipe(takeUntil(this.ngUnsubcribe))
        .subscribe({
          next: (data: any) => {
            this.progressing = false;
            console.log('data: ', data);
            this.router.navigate(['/staff/detail', this.staff['_id']]);
          },
          error: (data: any) => {
            console.log('data: ', data);
            this.progressing = false;
          }
        });
    } else {
      this.staffService.createStaff(this.staffForm.value)
        .pipe(takeUntil(this.ngUnsubcribe))
        .subscribe({
          next: (data: any) => {
            this.progressing = false;
            console.log('data: ', data);
            this.router.navigate(['/staff']);
          },
          error: (data: any) => {
            console.log('data: ', data);
            this.progressing = false;
          }
        });
    }
  }
}
