import { Injectable } from '@angular/core';
import { Resolve, RouterStateSnapshot, ActivatedRouteSnapshot } from '@angular/router';import { empty } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { KitchenStaffManagerService } from '../kitchen-staff-manager.service';

@Injectable()
export class KitchenStaffDetailResolver implements Resolve<any> {
  constructor(private kitchentaffManagerService: KitchenStaffManagerService) { }

  resolve(route: ActivatedRouteSnapshot, _state: RouterStateSnapshot) {
    console.log('router', route.params);
    return this.kitchentaffManagerService.getStaff(route.params['id']).pipe(
      catchError((error) => {
        return empty();
      })
    );
  }
}
