import { Component, OnInit, OnDestroy } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { ConfirmDeleteComponent } from '../confirm-delete/confirm-delete.component';
import { from, Subject } from 'rxjs';
import { filter, takeUntil } from 'rxjs/operators';
import { AuthUserService } from 'src/app/core/auth-user.service';
import { KitchenStaffManagerService } from '../kitchen-staff-manager.service';
import { IUser } from 'src/app/core/user.model';

@Component({
  selector: 'app-user-list',
  templateUrl: './user-list.component.html'
})
export class UserListComponent implements OnInit, OnDestroy {
  private ngUnsubcribe = new Subject();
  user: IUser;
  constructor(public staffManagerService: KitchenStaffManagerService
    , private ModalService: NgbModal, private authUserService: AuthUserService) {
  }

  ngOnInit(): void {
    this.user = this.authUserService.user;
    this.getStaffList()
  }

  ngOnDestroy() {
    this.ngUnsubcribe.next();
    this.ngUnsubcribe.complete();
  }


  deletestaff(staff: any): void {
    const modalRef = this.ModalService.open(ConfirmDeleteComponent, {
      size: 'lg',
      backdrop: 'static'
    });
    modalRef.componentInstance.staff = staff;
    from(modalRef.result)
      .pipe(
        filter((data: any) => data),
        takeUntil(this.ngUnsubcribe))
      .subscribe(() =>this.getStaffList()
      );
  }
  private getStaffList(): void {
    if (this.authUserService.user.role == 'Vendor') {
      this.staffManagerService.getStaffListForVendor(this.authUserService.user._id);

    } else if (this.authUserService.user.role == 'Manager') {
      this.staffManagerService.getStaffListForVendor(this.authUserService.user && this.authUserService.user['owner_id']);

    } else {
      this.staffManagerService.getStaffList();

    }
  }
}
