import { Component, OnInit, OnDestroy, Input } from '@angular/core';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { KitchenStaffManagerService } from '../kitchen-staff-manager.service';

@Component({
  selector: 'app-confirm-delete',
  templateUrl: './confirm-delete.component.html'
})
export class ConfirmDeleteComponent implements OnInit, OnDestroy {
  private ngUnsubcribe = new Subject();

  @Input()
  staff: any;

  constructor(private activeModal: NgbActiveModal, private staffManagerService: KitchenStaffManagerService) { }

  ngOnInit(): void {

  }

  ngOnDestroy() {
    this.ngUnsubcribe.next();
    this.ngUnsubcribe.complete();
  }

  delete(): void {
    this.staffManagerService.deleteStaff(this.staff.staff_id)
      .pipe(takeUntil(this.ngUnsubcribe))
      .subscribe((data: any) => {
        this.activeModal.close(true);
        console.log('data: ', data.staff);
      });
  }

  cancel(): void {
    this.activeModal.close(false);
  }
}
