import { Injectable } from '@angular/core';
import { TableState } from '../core/table-state';
import { BehaviorSubject, Observable } from 'rxjs';
import { StaffService } from '../core/staff.service';
import { VendorService } from '../core/vendor.service';
import { AreaService } from '../core/area.service';

@Injectable()
export class KitchenStaffManagerService {


  public state: TableState = {
    page: 1,
    pageSize: 10,
    searchTerm: '',
    sortColumn: '',
    sortDirection: ''
  }

  private _total$ = new BehaviorSubject<number>(1);
  get total$(): Observable<number> {
    return this._total$.asObservable();
  }

  private originalList = [];

  private _staffList$ = new BehaviorSubject<any[]>(null);
  get staffList$(): Observable<any[]> {
    return this._staffList$.asObservable();
  }


  private _vendorList$ = new BehaviorSubject<any[]>(null);
  get vendorList$(): Observable<any[]> {
    return this._vendorList$.asObservable();
  }


  private _areaList$ = new BehaviorSubject<any[]>(null);
  get areaList$(): Observable<any[]> {
    return this._areaList$.asObservable();
  }

  constructor(private staffService: StaffService, private vendorService: VendorService, private areaService: AreaService) {
    this.getVendorList();

  }

  createStaff(user: any): Observable<any> {
    const staff = {
      name: user['name'],
      email: user['email'],
      password: user['password'],
      city: user['city'],
      mobile: user['mobile'],
      status: user['status'],
      owner_id: user['owner_id'],
      role: user['role']

    };
    return this.staffService.createStaff(staff);
  }

  updateStaff(user: any): Observable<any> {
    const staff = {
      staff_id: user['staff_id'],
      name: user['name'],
      email: user['email'],
      city: user['city'],
      password: user['password'],
      mobile: user['mobile'],
      status: user['status'],
      owner_id: user['owner_id'],
      role: user['role']
    };
    return this.staffService.updateStaff(staff);
  }

  deleteStaff(id: string): Observable<any> {
    return this.staffService.deleteStaff(id);
  }

  getStaff(id: string): Observable<any> {
    return this.staffService.getStaff(id);
  }


  getStaffList(): void {
    this.staffService.getStaffList()
      .subscribe((data: any[]) => {
        console.log('Got Staffs for you: ', data);
        this._staffList$.next(data);
        this._total$.next(data.length);
        this.originalList = data;
      });
  }

  getStaffListForVendor(owner_id: string): void {
    this.staffService.getStaffListForVendor(owner_id)
      .subscribe((data: any[]) => {
        console.log('Got Staffs for you: ', data);
        this._staffList$.next(data);
        this._total$.next(data.length);
        this.originalList = data;
      });
  }

  getVendorList(): void {
    this.vendorService.getVendorList()
      .subscribe((data: any[]) => {
        console.log('Got Vendors for you: ', data);

        this._vendorList$.next(data);
      });
  }
  getAreaList(): void {
    this.areaService.getAreaList()
      .subscribe((data: any[]) => {
        console.log('Got Areas for you: ', data);

        this._areaList$.next(data);
      });
  }

  searchText(): void {
    if (!this.state.searchTerm) {
      this._staffList$.next(this.originalList);
      this._total$.next(this.originalList.length)
    } else {
      const searchTerm = this.state.searchTerm.toLocaleLowerCase();
      const filteredList = this.originalList.filter((item: any) => item['name'] && item['name'].toLocaleLowerCase().includes(searchTerm) ||
        item['email'] && item['email'].toLocaleLowerCase().includes(searchTerm) ||
        item['role'] && item['role'].toLocaleLowerCase().includes(searchTerm) ||
        item['mobile'] && item['mobile'].toLocaleLowerCase().includes(searchTerm) ||
        item['status'] && item['status'].toLocaleLowerCase().includes(searchTerm));
        
      this._staffList$.next(filteredList);
      this._total$.next(filteredList.length);
    }
  }

}
